import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { InterestRateManage } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-manage/bean/interest-rate-manage.bean";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
    selector: 'interest-rate-manage',
    templateUrl: './interest-rate-manage.component.html',
    styleUrls: ['./interest-rate-manage.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class InterestRateManageComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    user: any;
    orgId: string = '';
    userId: string = '';
    list_Table: any = [];
    fromstartDate: Date = null;
    fromendDate: Date = null;
    thrustartDate: Date = null;
    thruendDate: Date = null;
    addPage: boolean = false;
    personPage: boolean = false;//创建人选择
    personName: string;//创建人
    listOptions: any = [];//清单状态
    dataSourceOptions: any = [];//数据来源
    queryUserInValue: any;
    //选择数据
    chooseData: any = [];
    createUserName: any;
    display: boolean = false;
    isAll: boolean = false;
    checkAll: boolean = false;
    orgTreeDisplay2: boolean = false;
    addHeader = '选择字段';
    parentList: any = [];//利率上级类型名称
    firstchildList: any = [];//利率上级类型名称
    secondchildList: any = [];//利率上级类型名称
    thirdchildList: any = [];//利率上级类型名称
    fourthchildList: any = [];//利率上级类型名称
    parentId: any;//利率上级类型名称
    firstchildId: any;//利率上级类型名称
    secondchildId: any;//利率上级类型名称
    thirdchildId: any;//利率上级类型名称
    fourthchildId: any;//利率上级类型名称
    interestratemanage: InterestRateManage = new InterestRateManage();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        this.querysuper('',0);
        this.codeVal();
        this.listOptions = this.code['Source_stat'];//清单状态
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //利率类型上级查询
    querysuper(item, col) {
        let params = {
            // rateTypeName:	//利率类型名称
            // rateTypeCode:	//利率类型编号
            parentId: item,//利率类型上级Id

        }
        console.log(item, col);
        this.httpService.selectRateTypeList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": '' });
                data.priceTypeList.forEach((v) => {
                    List.push({ "label": v.rateTypeName, "value": v.rateTypeId, "isLeader": v.isLeader, "rateTypeCode":v.rateTypeCode });
                })
                if (item == '' && col == 0) {
                    this.parentList = List;
                } else if (col == 1) {
                    if(item && (item != null || item != '')) {
                        this.firstchildList = List;
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    } else {
                        this.parentId = '';
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 2) {
                    if(item && (item != null || item != '')) {
                        this.secondchildList = List;
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    } else {
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 3) {
                    if(item && (item != null || item != '')) {
                        this.thirdchildList = List;
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    } else {
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 4) {
                    if(item && (item != null || item != '')) {
                        this.fourthchildList = List;
                        this.fourthchildId = '';
                    } else {
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                }
                if (this.fourthchildId && this.fourthchildId != null) {
                    for(let i = 0; i< this.fourthchildList.length; i++){
                        if(this.fourthchildId == this.fourthchildList[i].value){
                            this.interestratemanage.rateTypeCode = this.fourthchildList[i].rateTypeCode;
                            break;
                        }
                    }
                } else if (this.thirdchildId && this.thirdchildId != null) {
                    for(let i = 0; i< this.thirdchildList.length; i++){
                        if(this.thirdchildId == this.thirdchildList[i].value){
                            this.interestratemanage.rateTypeCode = this.thirdchildList[i].rateTypeCode;
                            break;
                        }
                    }
                } else if (this.secondchildId && this.secondchildId != null) {
                    for(let i = 0; i< this.secondchildList.length; i++){
                        if(this.secondchildId == this.secondchildList[i].value){
                            this.interestratemanage.rateTypeCode = this.secondchildList[i].rateTypeCode;
                            break;
                        }
                    }
                } else if (this.firstchildId && this.firstchildId != null) {
                    for(let i = 0; i< this.firstchildList.length; i++){
                        if(this.firstchildId == this.firstchildList[i].value){
                            this.interestratemanage.rateTypeCode = this.firstchildList[i].rateTypeCode;
                            break;
                        }
                    }
                } else {
                    for(let i = 0; i< this.parentList.length; i++){
                        if(this.parentId == this.parentList[i].value){
                            this.interestratemanage.rateTypeCode = this.parentList[i].rateTypeCode;
                            break;
                        }
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: '调用服务失败！' });
        })
       
    }
    //查询
    query() {
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectPriceRatePurposelist();
    }
    paginate(event){

    }
    selectPriceRatePurposelist() {
        
        let params = {
            rateTypeName: this.interestratemanage.rateTypeName,//利率类型名称
            rateTypeCode: this.interestratemanage.rateTypeCode,//利率类型编号
            rateTypeId: this.interestratemanage.rateTypeId,//	利率类型ID
            ratePurposeId: this.interestratemanage.ratePurposeId,//	利率ID
            ratePurposeName: this.interestratemanage.ratePurposeName,//	利率名称
            rateValue: this.interestratemanage.rateValue,//	利率值
            fromDateBegin: this.commfunc.transDateN(this.fromstartDate),//	开始日期起
            fromDateEnd: this.commfunc.transDateN(this.fromendDate),//开始日期止
            thruDateBegin: this.commfunc.transDateN(this.thrustartDate),//	结束日期起
            thruDateEnd: this.commfunc.transDateN(this.thruendDate),//结束日期止
            rateStatusCode: this.interestratemanage.rateStatusCode,//审批状态
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectPriceRatePurposelist(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.returnList;
                for(let l=0; l<this.list_Table.length; l++){
                    this.list_Table[l].fromDate = this.commfunc.transDateN(this.list_Table[l].fromDate);
                    this.list_Table[l].thruDate = this.commfunc.transDateN(this.list_Table[l].thruDate);
                }
                this.total = data.totalNum;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '类型上级查询成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: '调用服务失败！' });
        })
    }
    //重置
    reset() {
        this.interestratemanage = new InterestRateManage();
        this.fromstartDate = null;
        this.fromendDate = null;
        this.thrustartDate = null;
        this.thruendDate = null;
        this.parentId = '';
        this.firstchildId = '';
        this.secondchildId = '';
        this.thirdchildId = '';
        this.fourthchildId = '';
    }
    //新增
    addBtn() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-allocation/interest-rate-madd',{ title: '利率新增'}])
    }
    //详情
    toDetail(col) {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-allocation/interest-rate-madd',{ title: '利率详情', dataType: col.rateTypeId, dataPurpose: col.ratePurposeId, }])
    }
    //修改
    toUpdate(col) {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-allocation/interest-rate-madd',{ title: '利率修改', dataType: col.rateTypeId, dataPurpose: col.ratePurposeId, }])
    }
    //删除
    toDelete(col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                //条件
                let param = {
                    rateTypeId: col.rateTypeId,//利率类型Id
                    ratePurposeId: col.ratePurposeId,//	利率ID
                };
                this.httpService.removePriceRatePurpose(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.first = 0;
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                })
            }
        });
    }
}
