import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { InterestRateManage } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-manage/bean/interest-rate-manage.bean";
import * as _ from 'lodash';

@Component({
    selector: 'inerest-rate-madd',
    templateUrl: './inerest-rate-madd.component.html',
    styleUrls: ['./inerest-rate-madd.component.scss'],
    providers: [Commfunc]
})
export class InterestRateMaddComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    user: any;
    orgId: string = '';
    userId: string = '';
    list_Table: any = [];
    startDate: Date = null;
    endDate: Date = null;
    addPage: boolean = false;
    personPage: boolean = false;//创建人选择
    personName: string;//创建人
    listOptions: any = [];//清单状态
    dataSourceOptions: any = [];//数据来源
    queryUserInValue: any;
    //选择数据
    chooseData: any = [];
    createUserName: any;
    display: boolean = false;
    isAll: boolean = false;
    checkAll: boolean = false;
    orgTreeDisplay2: boolean = false;
    addHeader = '选择字段';
    title: any;//页面标题
    dataType: any;//父页面传递的数据
    dataPurpose: any;//父页面传递的数据
    parentList: any = [];//利率上级类型名称
    firstchildList: any = [];//利率上级类型名称
    secondchildList: any = [];//利率上级类型名称
    thirdchildList: any = [];//利率上级类型名称
    fourthchildList: any = [];//利率上级类型名称
    parentId: any;//利率上级类型名称
    firstchildId: any;//利率上级类型名称
    secondchildId: any;//利率上级类型名称
    thirdchildId: any;//利率上级类型名称
    fourthchildId: any;//利率上级类型名称
    rateValueList: any = [];
    interestratemanage: InterestRateManage = new InterestRateManage();
    constructor(
        public httpService: ValuationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        this.dataType = this.route.snapshot.params['dataType'];
        this.dataPurpose = this.route.snapshot.params['dataPurpose'];
        this.title = this.route.snapshot.params['title'];
        if (this.dataType && this.dataType != null) {
            this.selectPriceRatePurpose();
        }
        if(this.title == '利率新增'){
            this.querysuper('',0,1);
        }
        this.codeVal();
        this.listOptions = this.code['Source_stat'];//清单状态
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //修改页面数据初始化
    selectPriceRatePurpose() {
        let params = {
            rateTypeId: this.dataType,//利率类型Id
            ratePurposeId: this.dataPurpose,//利率ID
        }
        this.httpService.selectPriceRatePurpose(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.interestratemanage = data;
                this.interestratemanage.ratePurposeId = this.dataPurpose;
                this.rateValueList = data.rateValueList;
                for(let l=0; l<this.rateValueList.length; l++){
                    this.rateValueList[l].fromDate = this.commfunc.transDateN(this.rateValueList[l].fromDate);
                    this.rateValueList[l].thruDate = this.commfunc.transDateN(this.rateValueList[l].thruDate);
                }
                if(data.parentCode && data.parentCode != null){
                   data.parentCode = this.dataType+','+data.parentCode; 
                } else {
                   data.parentCode = this.dataType;
                }
                
                if (data.parentCode && data.parentCode != null) {
                    for (let i = data.parentCode.split(",").length - 1; i >= 0; i--) {
                        if (i == data.parentCode.split(",").length - 1) {
                            this.parentId = data.parentCode.split(",")[i];
                            this.querysuper('', 0,0);
                        } else if (i == data.parentCode.split(",").length - 2) {
                            this.querysuper(this.parentId, 1,0);
                            this.firstchildId = data.parentCode.split(",")[i];
                        } else if (i == data.parentCode.split(",").length - 3) {
                            this.querysuper(this.firstchildId, 2,0);
                            this.secondchildId = data.parentCode.split(",")[i];
                        } else if (i == data.parentCode.split(",").length - 4) {
                            this.querysuper(this.secondchildId, 3,0);
                            this.thirdchildId = data.parentCode.split(",")[i];
                        } else if (i == data.parentCode.split(",").length - 5) {
                            this.querysuper(this.thirdchildId, 4,0);
                            this.fourthchildId = data.parentCode.split(",")[i];
                        }
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: '调用服务失败！' });
        })
    }
    //利率类型上级查询
    querysuper(item, col, index) {
        let params = {
            parentId: item,//利率类型上级Id

        }
        console.log(item, col);
        this.httpService.selectRateTypeList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": '' });
                data.priceTypeList.forEach((v) => {
                    List.push({ "label": v.rateTypeName, "value": v.rateTypeId, "isLeader": v.isLeader, "rateTypeCode":v.rateTypeCode });
                })
                if (item == '' && col == 0) {
                    this.parentList = List;
                } else if (col == 1) {
                    if(item && (item != null || item != '')) {
                        this.firstchildList = List;
                        if(index){
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.parentId = '';
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 2) {
                    if(item && (item != null || item != '')) {
                        this.secondchildList = List;
                        if(index){
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 3) {
                    if(item && (item != null || item != '')) {
                        this.thirdchildList = List;
                        if(index){
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 4) {
                    if(item && (item != null || item != '')) {
                        this.fourthchildList = List;
                        if(index){
                        this.fourthchildId = '';}
                    } else {
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: '调用服务失败！' });
        })
    }
    //保存
    save() {
        if (this.fourthchildId && this.fourthchildId != null) {
            this.interestratemanage.rateTypeId  = this.fourthchildId;
        } else if (this.thirdchildId && this.thirdchildId != null) {
            this.interestratemanage.rateTypeId  = this.thirdchildId;
        } else if (this.secondchildId && this.secondchildId != null) {
            this.interestratemanage.rateTypeId  = this.secondchildId;
        } else if (this.firstchildId && this.firstchildId != null) {
            this.interestratemanage.rateTypeId  = this.firstchildId;
        } else {
            this.interestratemanage.rateTypeId  = this.parentId;
        }
        //利率编号不能为空
        if (!this.interestratemanage.ratePurposeId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率编号不能为空' });
            return;
        }
        //利率名称不能为空
        if (!this.interestratemanage.ratePurposeName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率名称不能为空' });
            return;
        }
        //利率类型名称不能为空
        if (!this.interestratemanage.rateTypeId) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率类型名称不能为空' });
            return;
        }
        for (let l = 0; l < this.rateValueList.length; l++) {
            this.rateValueList[l].fromDate = this.commfunc.transDateN(this.rateValueList[l].fromDate);
            this.rateValueList[l].thruDate = this.commfunc.transDateN(this.rateValueList[l].thruDate);
        }
        let params = {
            rateTypeId: this.interestratemanage.rateTypeId, //利率类型ID
            ratePurposeId: this.interestratemanage.ratePurposeId, //	利率ID
            ratePurposeName: this.interestratemanage.ratePurposeName, //利率名称
            rateValueList: this.rateValueList, //	利率取值列表
        }
        if (this.title == '利率新增') {
            this.httpService.createPriceRatePurpose(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '利率新增成功', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '利率新增失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率新增失败', detail: '调用服务失败！' });
            })
        } else if (this.title == '利率修改') {
            this.httpService.updatePriceRatePurpose(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '利率修改成功', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '利率修改失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '利率修改失败', detail: '调用服务失败！' });
            })
        }
    }
    //增加一条
    addBtn() {
        let aa = _.clone(this.rateValueList);
        aa.push({
            rateValueCode: '', fromDate: '', thruDate: '', remark: ''
        });
        this.rateValueList = aa;
    }
    //删除一条
    toDelete(i, col) {
        // if (col.conditionalDimensionSeqCode && col.conditionalDimensionSeqCode != null) {
        //     let params = {
        //         conditionalDimensionSeqCode: col.conditionalDimensionSeqCode,
        //         conditionalDimensionCode: col.conditionalDimensionCode
        //     }
        //     this.httpService.removeProductPriceCond(params).subscribe(data => {
        //         if (data.returnCode.code == 'success') {
        //             this.msgs = [];
        //             this.msgs.push({ severity: 'success', summary: '条件维度种类删除成功', detail: data.returnCode.message });
        //         } else {
        //             this.msgs = [];
        //             this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: data.returnCode.message });
        //         }
        //     }, error => {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '条件维度种类删除失败', detail: '调用服务失败！' });
        //     })
        // }
        this.rateValueList.splice(i, 1);
        this.rateValueList = _.clone(this.rateValueList);
    }
    //返回
    cancel() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-allocation/interest-rate-manage'])
    }
}
