import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { InterestRateType } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-type/bean/interest-rate-type.bean";

@Component({
    selector: 'inerest-rate-tadd',
    templateUrl: './inerest-rate-tadd.component.html',
    styleUrls: ['./inerest-rate-tadd.component.scss'],
    providers: [Commfunc]
})
export class InterestRateTaddComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    user: any;
    orgId: string = '';
    userId: string = '';
    list_Table: any = [];
    startDate: Date = null;
    endDate: Date = null;
    addPage: boolean = false;
    personPage: boolean = false;//创建人选择
    personName: string;//创建人
    listOptions: any = [];//清单状态
    dataSourceOptions: any = [];//数据来源
    queryUserInValue: any;
    //选择数据
    chooseData: any = [];
    createUserName: any;
    display: boolean = false;
    isAll: boolean = false;
    checkAll: boolean = false;
    orgTreeDisplay2: boolean = false;
    addHeader = '选择字段';
    title: any;//页面标题
    data: any;//父页面传递的数据
    parentList: any = [];//利率上级类型名称
    firstchildList: any = [];//利率上级类型名称
    secondchildList: any = [];//利率上级类型名称
    thirdchildList: any = [];//利率上级类型名称
    fourthchildList: any = [];//利率上级类型名称
    parentId: any;//利率上级类型名称
    firstchildId: any;//利率上级类型名称
    secondchildId: any;//利率上级类型名称
    thirdchildId: any;//利率上级类型名称
    fourthchildId: any;//利率上级类型名称
    interestratetype: InterestRateType = new InterestRateType();
    constructor(
        public httpService: ValuationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (this.user.orgId) {
            this.orgId = this.user.orgId;
        }
        if (this.user.userId) {
            this.userId = this.user.userId;
        }
    }

    ngOnInit() {
        this.data = this.route.snapshot.params['data'];
        this.title = this.route.snapshot.params['title'];
        if (this.data && this.data != null) {
            this.selectPriceRateType();
        }
        this.codeVal();
        // this.listOptions = this.code['Source_stat'];//清单状态
        this.dataSourceOptions = this.code['Data_Source'];//数据来源
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //修改页面数据初始化
    selectPriceRateType() {
        let params = {
            rateTypeId: this.data//利率类型Id
        }
        this.httpService.selectPriceRateType(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.interestratetype = data;
                if (this.interestratetype.parentId && this.interestratetype.parentId != null) {
                    this.interestratetype.isLeader = '1';
                    debugger;
                    for (let i = this.interestratetype.parentId.split(",").length - 1; i >= 0; i--) {
                        if (i == this.interestratetype.parentId.split(",").length - 1) {
                            this.parentId = this.interestratetype.parentId.split(",")[i];
                            this.querysuper('', 0,0);
                        } else if (i == this.interestratetype.parentId.split(",").length - 2) {
                            this.firstchildId = this.interestratetype.parentId.split(",")[i];
                            this.querysuper(this.parentId, 1,0);
                        } else if (i == this.interestratetype.parentId.split(",").length - 3) {
                            this.secondchildId = this.interestratetype.parentId.split(",")[i];
                            this.querysuper(this.firstchildId, 2,0);
                        } else if (i == this.interestratetype.parentId.split(",").length - 4) {
                            this.thirdchildId = this.interestratetype.parentId.split(",")[i];
                            this.querysuper(this.secondchildId, 3,0);
                        } else if (i == this.interestratetype.parentId.split(",").length - 5){
                            this.fourthchildId = this.interestratetype.parentId.split(",")[i];
                            this.querysuper(this.thirdchildId, 4,0);
                        }

                    }
                } else {
                    this.interestratetype.isLeader = '0';
                }

                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '基准利率成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基准利率类型查询失败', detail: '调用服务失败！' });
        })
    }
    isLeader() {
        if (this.interestratetype.isLeader == '0') {
            this.interestratetype.parentId = '';
            this.parentId = '';
            this.firstchildId = '';
            this.secondchildId = '';
            this.thirdchildId = '';
            this.fourthchildId = '';
        } else {
            this.querysuper('', 0,1);
        }
    }
    //触发时停止动作
    // stop(col) {
    //     event.stopPropagation();
    //     this.querysuper(col);
    // }
    //利率类型上级查询
    querysuper(item, col,index) {
        let params = {
            // rateTypeName:	//利率类型名称
            // rateTypeCode:	//利率类型编号
            parentId: item,//利率类型上级Id

        }
        console.log(item, col);
        this.httpService.selectRateTypeList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": '' });
                data.priceTypeList.forEach((v) => {
                    List.push({ "label": v.rateTypeName, "value": v.rateTypeId, "isLeader": v.isLeader, "rateTypeCode":v.rateTypeCode  });
                })
                if (item == '' && col == 0) {
                    this.parentList = List;
                } else if (col == 1) {
                    if(item && (item != null || item != '')) {
                        this.firstchildList = List;
                        if(index){
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.parentId = '';
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 2) {
                    if(item && (item != null || item != '')) {
                        this.secondchildList = List;
                        if(index){
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.firstchildId = '';
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 3) {
                    if(item && (item != null || item != '')) {
                        this.thirdchildList = List;
                        if(index){
                        this.thirdchildId = '';
                        this.fourthchildId = '';}
                    } else {
                        this.secondchildId = '';
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                } else if (col == 4) {
                    if(item && (item != null || item != '')) {
                        this.fourthchildList = List;
                        if(index){
                        this.fourthchildId = '';}
                    } else {
                        this.thirdchildId = '';
                        this.fourthchildId = '';
                    }
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '类型上级查询失败', detail: '调用服务失败！' });
        })
    }
    //保存
    save() {
        //利率类型名称不能为空
        if (!this.interestratetype.rateTypeName) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率类型名称不能为空' });
            return;
        }
        //利率来源不能为空
        if (!this.interestratetype.rateSource) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率来源不能为空' });
            return;
        }
        //利率类型编号不能为空
        if (!this.interestratetype.rateTypeCode) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '利率类型编号不能为空' });
            return;
        }
        if (this.fourthchildId && this.fourthchildId != null) {
            for(let i = 0; i< this.fourthchildList.length; i++){
                if(this.fourthchildId == this.fourthchildList[i].value){
                    this.interestratetype.rateTypeCode = this.fourthchildList[i].rateTypeCode;
                    break;
                }
            }
        } else if (this.thirdchildId && this.thirdchildId != null) {
            for(let i = 0; i< this.thirdchildList.length; i++){
                if(this.thirdchildId == this.thirdchildList[i].value){
                    this.interestratetype.rateTypeCode = this.thirdchildList[i].rateTypeCode;
                    break;
                }
            }
        } else if (this.secondchildId && this.secondchildId != null) {
            for(let i = 0; i< this.secondchildList.length; i++){
                if(this.secondchildId == this.secondchildList[i].value){
                    this.interestratetype.rateTypeCode = this.secondchildList[i].rateTypeCode;
                    break;
                }
            }
        } else if (this.firstchildId && this.firstchildId != null) {
            for(let i = 0; i< this.firstchildList.length; i++){
                if(this.firstchildId == this.firstchildList[i].value){
                    this.interestratetype.rateTypeCode = this.firstchildList[i].rateTypeCode;
                    break;
                }
            }
        } else {
            for(let i = 0; i< this.parentList.length; i++){
                if(this.parentId == this.parentList[i].value){
                    this.interestratetype.rateTypeCode = this.parentList[i].rateTypeCode;
                    break;
                }
            }
        }
        if (this.title == '基准利率类型新增') {
            let params = {
                rateTypeName: this.interestratetype.rateTypeName, //利率类型名称
                rateTypeCode: this.interestratetype.rateTypeCode,	//利率类型编号
                rateSource: this.interestratetype.rateSource, //基准利率来源
                parentId: this.interestratetype.parentId,	//利率类型上级Id

            }
            this.httpService.createPriceRateType(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '基准利率类型新增成功', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '基准利率类型新增失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率类型新增失败', detail: '调用服务失败！' });
            })
        } else if (this.title == '基准利率类型修改') {
            let params = {
                rateTypeId: this.data, //利率类型Id
                rateTypeName: this.interestratetype.rateTypeName, //利率类型名称
                rateTypeCode: this.interestratetype.rateTypeCode, //利率类型编号
                rateSource: this.interestratetype.rateSource, //基准利率来源
                parentId: this.interestratetype.parentId,	//利率类型上级Id
            }
            this.httpService.updatePriceRateType(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '基准利率类型修改成功', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '基准利率类型修改失败', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率类型修改失败', detail: '调用服务失败！' });
            })
        }
    }
    //返回
    cancel() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-allocation/interest-rate-type'])
    }
}
