import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { InterestRateMaddComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-manage/inerest-rate-madd/inerest-rate-madd.component";
import { InterestRateTaddComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-type/inerest-rate-tadd/inerest-rate-tadd.component";
import { InterestRateTypeComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-type/interest-rate-type.component";
import { InterestRateManageComponent } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-allocation/interest-rate-manage/interest-rate-manage.component";

export const routes: Routes = [
    {
        path: '',

        children: [
            { path: '', redirectTo: 'interest-rate-allocation', pathMatch: 'full' },
            { path: 'interest-rate-madd',  component: InterestRateMaddComponent},
            { path: 'interest-rate-tadd',  component: InterestRateTaddComponent},
            { path: 'interest-rate-type',  component: InterestRateTypeComponent},
            { path: 'interest-rate-manage', component: InterestRateManageComponent }
        ]
    },
]
export const InterestRateAllocatRouting: ModuleWithProviders = RouterModule.forChild(routes);